.footer__text {
  color: #ffffff;
  line-height: 1.6em;
  font-weight: normal;
  font-size: 11px;
  text-align: center;
}

.footer {
  padding: 50px 0;
  position: relative;
  background-image: url("../images/footerBg.svg");
  background-position: top center;
  background-size: cover;
}

.footer__backTop {
  position: absolute;
  bottom: 50px;
  right: 20px;
  color: #c5c5c5;
  font-size: 12px;
  background: rgba(0, 0, 0, 0.25);
  padding: 10px 20px;
  border-radius: 5px;
  margin: 0;
}



